<template>
    <!-- Page wrapper start -->
    <div class="page-wrapper">

      <!-- Sidebar wrapper start -->
      <Sidebar/>
      <!-- Sidebar wrapper end -->

      <!-- *************
        ************ Main container start *************
      ************* -->
      <div class="main-container">

        <!-- Page header starts -->
        <Navbar page="Recursos Humanos / Detalhes da Folha Salarial"/>
        <!-- Page header ends -->

        <!-- Content wrapper scroll start -->
        <div class="content-wrapper-scroll">

          <!-- Content wrapper start -->
          <div class="content-wrapper">

            <!-- Row start -->
            <div class="row">
              <div class="col-12">
                <div class="card">
                  <div class="d-flex p-3">
                    <button style = "background:black" @click="goBack" class="btn text-white btn-sm mx-1 fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                      <span class="fas fa-chevron-left mx-1 fs-5"></span> Voltar
                    </button>
                    <button @click="selectRow" data-bs-toggle="modal" data-bs-target="#removeForm" class="btn btn-success text-white btn-sm mx-1 fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                      <span class="fa fa-file-pdf mx-1 fs-5"></span> Processar 
                    </button>
                    <button @click="selectRow" data-bs-toggle="modal" data-bs-target="#removeForm" class="btn btn-danger text-white btn-sm mx-1 fw-bold d-flex align-items-center p-2 rounded-3 border-0 text-uppercase">
                      <span class="fa fa-print mx-1 fs-5"></span> Imprimir 
                    </button>
                  </div>
                  <div class="card-body pt-0">
                     <div class="row">
                        <div class="col-md-6">
                            <label>Ano letivo</label><br>
                            <select class="form-select" v-model="salario.ano_lectivo_id" readonly disabled>
                                <option 
                                    v-for="ano in anos" 
                                    :key="ano.id"     
                                    :value="ano.id"
                                >{{ano.nome}}</option>    
                            </select>
                        </div>
                        <div class="col-md-6">
                            <label>Mês</label><br>
                            <select class="form-select" v-model="salario.mes" readonly disabled>
                                <option 
                                    v-for="mes in meses" 
                                    :key="mes"     
                                    :value="mes"
                                >{{mes}}</option>    
                            </select>
                        </div>
                     </div>
                     <form @submit.prevent = "sendData">
                         <table class="table table-striped table-bordered mt-2">
                            <thead>
                                <tr>
                                    <th>FUNCIONÁRIO</th>
                                    <th>S.BASE</th>
                                    <th>INSS</th>
                                    <th>IRT</th>
                                    <th>FALTAS</th>
                                    <th>SUBSÍDIOS</th>
                                    <th>S.LÍQUIDO</th>
                                    <th>OPÇÕES</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(input, index) in folhasSalariais" :key="index">
                                    <td>
                                        {{input.Funcionario.nome}}
                                    </td>
                                    <td class = "pt-3">
                                        <h6>{{ numberFormat(folhasSalariais[index].salario_base,2,',','.') }}</h6>
                                    </td>
                                    <td>
                                        <input v-model="folhasSalariais[index].inss" type="number" class="form-control" @input="onInputChange(index)">
                                    </td>
                                    <td>
                                        <input v-model="folhasSalariais[index].irt" type="number" class="form-control" @input="onInputChange(index)">
                                    </td>
                                    <td>
                                        <input v-model="folhasSalariais[index].faltas" type="number" class="form-control" @input="onInputChange(index)">
                                    </td>
                                    <td>
                                        <input v-model="folhasSalariais[index].subsidios" type="number" class="form-control" @input="onInputChange(index)">
                                    </td>
                                    <td class="pt-3">
                                        <h6>{{ numberFormat(folhasSalariais[index].salario_liquido, 2, ',', '.') }}</h6>
                                    </td>
                                    <td>
                                        <button type="button" @click="removeRow(index)" class="btn btn-danger">
                                            <span class="fas fa-trash"></span>
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <div class="d-flex float-end">
                            <button type="submit" class="btn btn-sm btn-success mx-2 fw-bold text-uppercase">
                                <span class="fas fa-database"></span> Atualizar Dados
                            </button>
                        </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
            <!-- Row end -->
          </div>
          <!-- Content wrapper end -->

          <!-- App Footer start -->
          <Footer/>
          <!-- App footer end -->

        </div>
        <!-- Content wrapper scroll end -->

      </div>
    </div>
    <!-- Page wrapper end -->
</template>

<script>
import '../../static';
import Sidebar from '../../components/Sidebar.vue';
import Navbar from '../../components/Navbar.vue';
import Footer from '../../components/Footer.vue';
import { api, numberFormat } from '../../../../helpers/api';
import { mapState } from 'vuex';

export default {
  name: "salary_sheet_detail_view",
  components: { Sidebar, Navbar, Footer },
  data() {
    return {
      title: "Detalhes da Folha",
      selectedRow: null,
      folhasSalariais: [],
      anos: [],
      meses: ['Janeiro', 'Fevereiro', 'Março', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro'],
      salario: {},
    }
  },
  computed: {
    ...mapState(['user'])
  },
  mounted() {
    this.getFolhasSalariais();
    this.getSalarios();
    this.getAnos();
  },
  methods: {
    goBack(){
        this.$router.go(-1)
    },
    removeRow(index){
        this.$swal.fire({
            title:"Notificação",
            text:"Deseja Remover este item da lista",
            icon:"warning",
            showConfirmButton:true,
            showCancelButton:true,
            confirmButtonText:"Sim",
            cancelButtonText:"Não",
        })
        .then(res => {
            if(res.isConfirmed){
                this.deleteForm(this.folhasSalariais[index].id,index)
            }
        })
    },
    numberFormat,
    getAnos() {
      api.get(`/anos/`)
        .then(res => {
          this.anos = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getSalarios() {
      api.get(`/salarios/${this.$route.params.id}`)
        .then(res => {
          this.salario = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getFolhasSalariais() {
      api.get(`/salarios/salario/${this.$route.params.id}`)
        .then(res => {
            this.folhasSalariais = res.data;
            // Calcular o salário líquido para cada funcionário
            this.folhasSalariais.forEach((_, index) => {
                this.calcularSalarioLiquido(index);
            });
        })
        .catch(err => {
          console.log(err);
        });
    },
    deleteForm(id,index) {
      api.delete(`/salarios/folha-salarial/${id}`)
        .then(res => {
          if (res.data.title === true) {
            this.$swal.fire({
              title: "Sucesso",
              text: res.data.message,
              icon: "success"
            });
            this.folhasSalariais.splice(index, 1);
            this.getFolhasSalariais();
          } else {
            this.$swal.fire({
              title: "Erro: Não foi possível excluir",
              icon: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    calcularSalarioLiquido(index) {
        const salarioBase = this.folhasSalariais[index].salario_base;
        const inssPercent = this.folhasSalariais[index].inss / 100; 
        const irtPercent = this.folhasSalariais[index].irt / 100; 
        const faltas = this.folhasSalariais[index].faltas;
        const subsidios = this.folhasSalariais[index].subsidios;

        // Cálculo do salário ajustado
        const salarioAjustado = salarioBase - faltas + subsidios;

        // Cálculo dos descontos
        const descontoInss = salarioAjustado * inssPercent; 
        const descontoIrt = salarioAjustado * irtPercent; 

        // Cálculo do salário líquido
        const salarioLiquido = salarioAjustado - descontoInss - descontoIrt;

        // Atualiza o salário líquido na folha salarial
        this.folhasSalariais[index].salario_liquido = salarioLiquido;
    },

    // Chame este método sempre que os valores de INSS, IRT, faltas ou subsídios forem alterados
    onInputChange(index) {
        this.calcularSalarioLiquido(index);
    },

    sendData(){
        const form = {
            itens:this.folhasSalariais
        }
        api
        .put(`/salarios/folha-salarial/update-salarios/all`, form)
        .then(res => {
            if(res.data.title === true){
                this.$swal.fire({
                    title:"Sucesso",
                    text:res.data.message,
                    icon:"success"
                })
            }else{
                this.$swal.fire({
                    title:"Erro",
                    text:"Não foi possível atualizar",
                    icon:"error"
                }) 
            }
        })
        .catch(() => {
            this.$swal.fire({
                title:"Erro",
                text:"Não foi possível atualizar",
                icon:"error"
            }) 
        })
    }
  },
}
</script>

<style>
@import 'datatables.net-bs5';
</style>